export const paymentFilter = (payFrom, payTo, payCur) => {
  if (payFrom !== 0 && payTo !== 0) {
    if (+payFrom < +payTo) {
      return (
        <>
          <b className="jobCard__payment">
            /  {payFrom} - {payTo} {payCur}
          </b>
        </>
      );
    } else if (+payFrom > +payTo) {
      return (
        <>
          <b className="jobCard__payment">
            /  {payTo} - {payFrom} {payCur}
          </b>
        </>
      );
    } else if (+payFrom === +payTo) {
      return (
        <>
          <b className="jobCard__payment">
            / {payTo} {payCur}
          </b>
        </>
      );
    }
  } else if (payFrom !== 0 && payTo === 0) {
    return (
      <>
        <b className="jobCard__payment">
          / {payFrom} {payCur}
        </b>
      </>
    );
  } else if (payFrom === 0 && payTo !== 0) {
    return (
      <>
        <b className="jobCard__payment">
          / {payTo} {payCur}
        </b>
      </>
    );
  } else {
    return (
      <>
        <b className="jobCard__payment">/  </b>
      </>
    );
  }
};
